import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { OrderProvider } from '../../providers/order/order';

/**
 * Generated class for the SetupTypePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-setup-type',
  templateUrl: 'setup-type.html',
})
export class SetupTypePage {
  title = "Tạo";
  localType = "";
  constructor(private navCtrl: NavController, 
              private orderProvider: OrderProvider,
              private navParams: NavParams) {
  }
  saveData(){
    this.orderProvider.addType(this.localType);
    this.title = "Tạo";
  }
  editType(data: string){
    this.title = "Sửa";
    this.orderProvider.editType(data);
  }
  deleteType(data: string){
    this.orderProvider.deleteType(data);
  }
  cancelEdit(){
    this.localType = "";
    this.title = "Tạo";
  }
}



